<template>
  <div class="admin-content admin-content-edit">
    <div class="admin-content-top">
      <h2>  </h2>
    </div>
    <div class="admin-content-main">
      <div v-if="loading" class="loading">
        <img src="/img/preloader.svg" alt=" ">
      </div>

      <div v-else>
        <el-form ref="article" :model="article">

          <el-form-item prop="title">
            <label class="label" for="title"></label>
            <el-input id="title" type="text" class="input" placeholder=" " v-model="article.author_name" disabled="true"></el-input>
          </el-form-item>

          <el-form-item prop="title">
            <label class="label" for="title"></label>
            <el-input id="title" type="text" class="input" placeholder=" " v-model="article.title"></el-input>
          </el-form-item>

          <el-form-item prop="cut">
            <label class="label" for="cut"></label>
            <el-input id="cut" type="text" class="input" placeholder=" " v-model="article.cut"></el-input>
          </el-form-item>

          <el-form-item prop="text">
            <label class="label" for="text"></label>
            <el-input id="text" type="text" class="input" placeholder=" " v-model="article.text"></el-input>
          </el-form-item>

          <el-form-item prop="categories">
            <label class="label" for="categories"></label>
              <el-select id="categories" class="form-item-select" placeholder=" " multiple v-model="selectedCategories">
              <el-option
                v-for="category in this.categories"
                :key="category.id"
                :value="category.id"
                :label="category.name"
              >{{ category.name }}</el-option>
            </el-select>
          </el-form-item>

          <el-form-item class="one-button-row">
            <el-button class="button-save" type="primary" @click="submitForm('article')"></el-button>
          </el-form-item>

        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import api from '@/network/api'
import moment from 'moment'
moment.locale('ru')

export default {
  data: () => ({
    loading: true,
    search: '',
    selectedCategories: [],
    article: {
      id: '',
      author: '',
      title: '',
      cut: '',
      text: '',
      categories: [
        {
          id: '',
          name: ''
        }
      ],
      views: ''
    },
    rules: {
      author: [
        {
          required: true,
          message: '    ',
          trigger: 'blur'
        }
      ],
      title: [
        {
          required: true,
          message: '    ',
          trigger: 'blur'
        }
      ],
      cut: [
        {
          required: true,
          message: '    ',
          trigger: 'blur'
        }
      ],
      text: [
        {
          required: true,
          message: '    ',
          trigger: 'blur'
        }
      ]
    }
  }),

  async created () {
    const article = await api.getArticle(this.$store.getters.getAuthToken, this.$route.params.id)
    this.article = article.data

    this.article.author_name = article.data.author.first_name + ' ' + article.data.author.last_name

    this.loading = false
  },
  methods: {
    async submitForm (formName) {
      let validation = false

      this.$refs[formName].validate((valid) => {
        if (valid) {
          validation = true
        }
      })

      if (validation) {
        await api.updateAdminArticle (this.$store.getters.getAuthToken, this.article)

        alert('  !')

        this.$router.push({ name: 'ArticlesAdmin' })
      }
    }
  }
}
</script>
